'use client';

import React, { useState } from 'react';
import { Check, Copy } from 'lucide-react';

export interface CodeBlockProps {
  code: string;
  language: 'typescript' | 'python' | 'javascript' | 'bash';
  showLineNumbers?: boolean;
  highlightLines?: number[];
  filename?: string;
}

export function CodeBlock({
  code,
  language,
  showLineNumbers = false,
  highlightLines = [],
  filename,
}: CodeBlockProps) {
  const [copied, setCopied] = useState(false);

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(code);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error('Failed to copy code:', err);
    }
  };

  const lines = code.split('\n');

  return (
    <div className="relative rounded-lg bg-gray-900 dark:bg-gray-950 overflow-hidden">
      {filename && (
        <div className="px-4 py-2 bg-gray-800 dark:bg-gray-900 text-gray-300 text-sm font-mono border-b border-gray-700">
          {filename}
        </div>
      )}
      <div className="relative">
        <button
          onClick={handleCopy}
          className="absolute top-2 right-2 p-2 rounded-md bg-gray-800 hover:bg-gray-700 text-gray-300 transition-colors focus:outline-none focus:ring-2 focus:ring-primary"
          aria-label="Copy code"
        >
          {copied ? <Check size={16} /> : <Copy size={16} />}
        </button>
        <pre className="p-4 overflow-x-auto">
          <code className={`language-${language} text-sm`}>
            {showLineNumbers ? (
              <div className="flex">
                <div className="select-none text-gray-500 pr-4 text-right">
                  {lines.map((_, i) => (
                    <div key={i}>{i + 1}</div>
                  ))}
                </div>
                <div className="flex-1">
                  {lines.map((line, i) => (
                    <div
                      key={i}
                      className={highlightLines.includes(i + 1) ? 'bg-primary/20' : ''}
                    >
                      {line || '\n'}
                    </div>
                  ))}
                </div>
              </div>
            ) : (
              <div className="text-gray-100">{code}</div>
            )}
          </code>
        </pre>
      </div>
    </div>
  );
}
